"use client";

import { useState } from "react";
import Link from "next/link";
import {
  MdSchedule,
  MdTrendingUp,
  MdTrendingDown,
  MdPeople,
  MdClose,
  MdStar,
  MdCalendarToday,
  MdPayment,
  MdArrowForward,
  MdAccessTime,
  MdSavings,
} from "react-icons/md";
import { cn, formatCents, formatUnitsToHours } from "@/lib/utils";
import { Card, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Badge } from "@/components/ui/badge";
import {
  Sheet,
  SheetContent,
  SheetHeader,
  SheetTitle,
  SheetClose,
} from "@/components/ui/sheet";
import { Separator } from "@/components/ui/separator";
import { WalletBalanceCard, type WalletBalanceData } from "@/components/marketplace";
import { WalletBalanceListSkeleton } from "@/components/ui/skeletons";
import { EmptyState } from "@/components/ui/empty-state";

type WalletBalance = WalletBalanceData;

interface WalletTabProps {
  experienceId?: string;
  balances: WalletBalance[];
  isLoading?: boolean;
  standalone?: boolean; // When true, uses /app paths instead of /experiences
}

export function WalletTab({
  experienceId,
  balances,
  isLoading,
  standalone,
}: WalletTabProps) {
  // Generate base URLs based on context
  const browseUrl = standalone ? "/app?tab=browse" : `/experiences/${experienceId}?tab=browse`;
  const [selectedBalance, setSelectedBalance] = useState<WalletBalance | null>(
    null
  );

  // Calculate totals
  const totalHours = balances.reduce((sum, b) => sum + b.balanceUnits, 0) / 2;
  const totalValue = balances.reduce((sum, b) => {
    const currentRate = b.seller.sellerProfile?.hourlyRate || 0;
    return sum + (b.balanceUnits / 2) * currentRate;
  }, 0);
  const totalPaid = balances.reduce((sum, b) => sum + b.totalPaid, 0);
  const totalGainLoss = totalValue - totalPaid;
  const gainLossPercent =
    totalPaid > 0 ? ((totalValue - totalPaid) / totalPaid) * 100 : 0;

  return (
    <div className="p-4 sm:p-6 md:p-8 max-w-4xl mx-auto">
      {/* Page Header */}
      <div className="mb-8">
        <h1 className="text-2xl font-semibold text-foreground">Wallet</h1>
        <p className="text-muted-foreground mt-1">
          Your purchased hours and balances
        </p>
      </div>

      {/* Portfolio Overview Card */}
      <Card variant="bordered" className="mb-6">
        <CardContent className="p-6">
          <div className="flex flex-col sm:flex-row sm:items-center justify-between gap-6">
            {/* Primary Stat - Total Hours */}
            <div>
              <p className="text-sm font-medium text-muted-foreground mb-2">
                Total Hours
              </p>
              <div className="flex items-baseline gap-2">
                <span className="text-5xl font-bold tracking-tight text-foreground tabular-nums">
                  {totalHours}
                </span>
                <span className="text-lg text-muted-foreground">hrs</span>
              </div>
            </div>

            {/* Secondary Stats */}
            <div className="flex gap-8 sm:gap-12">
              <div>
                <p className="text-sm text-muted-foreground mb-1">Value</p>
                <p className="text-2xl font-semibold text-foreground tabular-nums">
                  {formatCents(totalValue)}
                </p>
              </div>
              <div>
                <p className="text-sm text-muted-foreground mb-1">Return</p>
                <div className="flex items-center gap-2">
                  <span
                    className={cn(
                      "text-2xl font-semibold tabular-nums",
                      totalGainLoss >= 0 ? "text-success" : "text-destructive"
                    )}
                  >
                    {totalGainLoss >= 0 ? "+" : ""}
                    {formatCents(totalGainLoss)}
                  </span>
                  {totalPaid > 0 && (
                    <Badge
                      variant={totalGainLoss >= 0 ? "success-subtle" : "destructive-subtle"}
                      className="text-xs"
                    >
                      {totalGainLoss >= 0 ? (
                        <MdTrendingUp className="h-3 w-3 mr-1" />
                      ) : (
                        <MdTrendingDown className="h-3 w-3 mr-1" />
                      )}
                      {Math.abs(gainLossPercent).toFixed(1)}%
                    </Badge>
                  )}
                </div>
              </div>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Quick Stats Row */}
      <div className="grid grid-cols-3 gap-3 mb-8">
        <div className="bg-card border border-border rounded-xl p-4 text-center">
          <div className="inline-flex h-9 w-9 items-center justify-center rounded-full bg-primary/10 mb-2">
            <MdSavings className="h-4 w-4 text-primary" />
          </div>
          <p className="text-xl font-semibold text-foreground tabular-nums">
            {formatCents(totalPaid)}
          </p>
          <p className="text-xs text-muted-foreground">Invested</p>
        </div>

        <div className="bg-card border border-border rounded-xl p-4 text-center">
          <div className="inline-flex h-9 w-9 items-center justify-center rounded-full bg-success/10 mb-2">
            <MdTrendingUp className="h-4 w-4 text-success" />
          </div>
          <p className="text-xl font-semibold text-foreground tabular-nums">
            {formatCents(totalValue)}
          </p>
          <p className="text-xs text-muted-foreground">Current Value</p>
        </div>

        <div className="bg-card border border-border rounded-xl p-4 text-center">
          <div className="inline-flex h-9 w-9 items-center justify-center rounded-full bg-violet-500/10 mb-2">
            <MdPeople className="h-4 w-4 text-violet-500" />
          </div>
          <p className="text-xl font-semibold text-foreground tabular-nums">
            {balances.length}
          </p>
          <p className="text-xs text-muted-foreground">
            {balances.length === 1 ? "Seller" : "Sellers"}
          </p>
        </div>
      </div>

      {/* Balances Section */}
      <div className="flex items-center justify-between mb-4">
        <h2 className="text-lg font-semibold text-foreground">Holdings</h2>
        {balances.length > 0 && (
          <Link href={browseUrl}>
            <Button variant="outline" size="sm" className="gap-1.5">
              Buy Hours
              <MdArrowForward className="h-4 w-4" />
            </Button>
          </Link>
        )}
      </div>

      {/* Balances List */}
      {isLoading ? (
        <WalletBalanceListSkeleton count={3} />
      ) : balances.length === 0 ? (
        <Card variant="bordered">
          <CardContent className="p-0">
            <EmptyState
              preset="no-wallet-balance"
              size="lg"
            >
              <Link href={browseUrl}>
                <Button size="lg" className="gap-2">
                  Browse Members
                  <MdArrowForward className="h-4 w-4" />
                </Button>
              </Link>
            </EmptyState>
          </CardContent>
        </Card>
      ) : (
        <div className="space-y-3">
          {balances.map((balance) => (
            <WalletBalanceCard
              key={balance.id}
              balance={balance}
              onClick={() => setSelectedBalance(balance)}
            />
          ))}
        </div>
      )}

      {/* Transaction Detail Sheet */}
      <Sheet
        open={!!selectedBalance}
        onOpenChange={(open) => !open && setSelectedBalance(null)}
      >
        <SheetContent side="right" width="lg" className="overflow-y-auto">
          {selectedBalance && (
            <TransactionDetailPanel
              balance={selectedBalance}
              experienceId={experienceId}
              standalone={standalone}
              onClose={() => setSelectedBalance(null)}
            />
          )}
        </SheetContent>
      </Sheet>
    </div>
  );
}

function TransactionDetailPanel({
  balance,
  experienceId,
  standalone,
  onClose,
}: {
  balance: WalletBalance;
  experienceId?: string;
  standalone?: boolean;
  onClose: () => void;
}) {
  // Generate URLs based on context
  const bookUrl = standalone
    ? `/book/${balance.seller.id}`
    : `/experiences/${experienceId}/book/${balance.seller.id}`;
  const profileUrl = standalone
    ? `/sellers/${balance.seller.id}`
    : `/experiences/${experienceId}/seller/${balance.seller.id}`;
  const seller = balance.seller;
  const currentRate = seller.sellerProfile?.hourlyRate || 0;
  const purchasedRate = balance.avgPurchasePricePerUnit * 2;
  const currentValue = (balance.balanceUnits / 2) * currentRate;
  const originalValue = (balance.balanceUnits / 2) * purchasedRate;
  const gainLoss = currentValue - originalValue;
  const gainLossPercent =
    originalValue > 0 ? ((currentValue - originalValue) / originalValue) * 100 : 0;

  return (
    <div className="h-full flex flex-col">
      {/* Header */}
      <SheetHeader className="py-6 border-b border-border">
        <div className="flex items-start justify-between">
          <div className="flex items-center gap-4">
            <Avatar className="h-14 w-14 border-2 border-border shadow-[var(--shadow-sm)]">
              <AvatarImage src={seller.avatar || undefined} />
              <AvatarFallback className="bg-primary/10 text-primary font-semibold text-lg">
                {seller.name?.[0]?.toUpperCase() || "?"}
              </AvatarFallback>
            </Avatar>
            <div>
              <SheetTitle className="text-xl font-semibold">
                {seller.name || "Anonymous"}
              </SheetTitle>
              <div className="flex items-center gap-2 mt-1">
                {seller.sellerProfile?.averageRating && (
                  <div className="flex items-center gap-1 text-sm text-muted-foreground">
                    <MdStar className="h-4 w-4 text-warning" />
                    <span>{seller.sellerProfile.averageRating.toFixed(1)}</span>
                  </div>
                )}
                {seller.sellerProfile?.totalSessionsCompleted && (
                  <>
                    <span className="text-border">Â·</span>
                    <span className="text-sm text-muted-foreground">
                      {seller.sellerProfile.totalSessionsCompleted} sessions
                    </span>
                  </>
                )}
              </div>
            </div>
          </div>
          <SheetClose asChild>
            <Button variant="ghost" size="icon" className="rounded-full -mr-2 -mt-2">
              <MdClose className="h-5 w-5" />
            </Button>
          </SheetClose>
        </div>
      </SheetHeader>

      {/* Content */}
      <div className="flex-1 py-6 space-y-6 overflow-y-auto">
        {/* Balance Summary */}
        <div className="bg-muted/30 rounded-xl p-5 border border-border">
          <div className="flex items-center justify-between mb-4">
            <p className="text-sm font-medium text-muted-foreground">
              Your Balance
            </p>
            {gainLoss !== 0 && (
              <Badge
                variant={gainLoss >= 0 ? "success-subtle" : "destructive-subtle"}
                className="gap-1"
              >
                {gainLoss >= 0 ? (
                  <MdTrendingUp className="h-3 w-3" />
                ) : (
                  <MdTrendingDown className="h-3 w-3" />
                )}
                {gainLoss >= 0 ? "+" : ""}
                {gainLossPercent.toFixed(1)}%
              </Badge>
            )}
          </div>
          <div className="flex items-baseline gap-2 mb-1">
            <span className="text-4xl font-bold text-foreground tabular-nums">
              {formatUnitsToHours(balance.balanceUnits)}
            </span>
          </div>
          <p className="text-sm text-muted-foreground">
            Worth{" "}
            <span className="font-semibold text-foreground">
              {formatCents(currentValue)}
            </span>{" "}
            at current rate
          </p>
        </div>

        {/* Rate Comparison */}
        <div>
          <h3 className="text-sm font-medium text-muted-foreground mb-3">
            Rate Comparison
          </h3>
          <div className="space-y-2">
            <div className="flex items-center justify-between p-3 bg-card border border-border rounded-lg">
              <div className="flex items-center gap-3">
                <div className="h-8 w-8 rounded-full bg-muted flex items-center justify-center">
                  <MdPayment className="h-4 w-4 text-muted-foreground" />
                </div>
                <span className="text-sm text-muted-foreground">Purchase rate</span>
              </div>
              <span className="font-semibold tabular-nums">{formatCents(purchasedRate)}/hr</span>
            </div>
            <div className="flex items-center justify-between p-3 bg-card border border-border rounded-lg">
              <div className="flex items-center gap-3">
                <div className="h-8 w-8 rounded-full bg-primary/10 flex items-center justify-center">
                  <MdTrendingUp className="h-4 w-4 text-primary" />
                </div>
                <span className="text-sm text-muted-foreground">Current rate</span>
              </div>
              <span className="font-semibold tabular-nums">{formatCents(currentRate)}/hr</span>
            </div>
          </div>
        </div>

        <Separator />

        {/* Value Breakdown */}
        <div>
          <h3 className="text-sm font-medium text-muted-foreground mb-3">
            Value Breakdown
          </h3>
          <div className="space-y-3">
            <div className="flex justify-between text-sm">
              <span className="text-muted-foreground">Amount invested</span>
              <span className="tabular-nums">{formatCents(balance.totalPaid)}</span>
            </div>
            <div className="flex justify-between text-sm">
              <span className="text-muted-foreground">Current value</span>
              <span className="tabular-nums">{formatCents(currentValue)}</span>
            </div>
            <Separator />
            <div className="flex justify-between font-medium">
              <span>Unrealized {gainLoss >= 0 ? "gain" : "loss"}</span>
              <span
                className={cn(
                  "tabular-nums",
                  gainLoss >= 0 ? "text-success" : "text-destructive"
                )}
              >
                {gainLoss >= 0 ? "+" : ""}
                {formatCents(Math.abs(gainLoss))}
              </span>
            </div>
          </div>
        </div>

        {/* Reserved Hours (if any) */}
        {balance.reservedUnits && balance.reservedUnits > 0 && (
          <>
            <Separator />
            <div className="bg-warning/5 border border-warning/20 rounded-lg p-4">
              <div className="flex items-center gap-2 text-warning mb-1">
                <MdAccessTime className="h-4 w-4" />
                <span className="text-sm font-medium">Reserved Hours</span>
              </div>
              <p className="text-sm text-muted-foreground">
                {formatUnitsToHours(balance.reservedUnits)} reserved for upcoming
                sessions
              </p>
            </div>
          </>
        )}

        {/* Quick Summary Grid */}
        <div>
          <h3 className="text-sm font-medium text-muted-foreground mb-3">
            Summary
          </h3>
          <div className="grid grid-cols-2 gap-3">
            <div className="bg-card border border-border rounded-lg p-4 text-center">
              <p className="text-2xl font-bold text-foreground tabular-nums">
                {balance.balanceUnits / 2}
              </p>
              <p className="text-xs text-muted-foreground mt-1">Hours available</p>
            </div>
            <div className="bg-card border border-border rounded-lg p-4 text-center">
              <p className="text-2xl font-bold text-foreground tabular-nums">
                {formatCents(currentValue)}
              </p>
              <p className="text-xs text-muted-foreground mt-1">Current value</p>
            </div>
          </div>
        </div>
      </div>

      {/* Footer Actions */}
      <div className="border-t border-border pt-4 pb-2 space-y-2">
        <Link
          href={bookUrl}
          className="block"
        >
          <Button className="w-full gap-2" size="lg">
            <MdCalendarToday className="h-4 w-4" />
            Book a Session
          </Button>
        </Link>
        <Link
          href={profileUrl}
          className="block"
        >
          <Button variant="outline" className="w-full gap-2">
            View Profile
            <MdArrowForward className="h-4 w-4" />
          </Button>
        </Link>
      </div>
    </div>
  );
}
